import React from "react";
import { useNavigate } from 'react-router-dom'; // import useNavigate
import './logout.css';

function LogoutX({ onLogout }) {
  const navigate = useNavigate(); // Utwrz instancj navigate

  const handleClick = e => {
    e.preventDefault();
    const confirmLogout = window.confirm('Czy na pewno chcesz si wylogowa?');
    if (confirmLogout) {
      localStorage.clear();
      console.log('Uytkownik wylogowany.');
      onLogout();  // Wywoanie przekazanej funkcji onLogout
      navigate('/');  // Przekierowanie do strony gwnej
    }
  }

  return (
    <div>
      <div className="EXIT">
        <button className="btn-1" id="logout" onClick={handleClick}>X</button>
      </div>
    </div>
  );
}

export default LogoutX;
